.items-in-cart {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: tomato;
    border-radius: 15px;
    font-size: var(--font-size-main);
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color-white);
    z-index: 2;
    left: 15px;
    top: -10px;
}